import React from 'react'
import "./Widgets.css"

function Widgets() {
    return (
        <div className= "widgets">
        <iframe 
            src="https://ndubuisx.com/" 
            width="340" 
            height="100%" 
            frameborder="0" 
            scrolling="no" allowtransparency="true"
            style={{border:"none", overflow:"hidden"}}>
            </iframe>
            
        </div>
    )
}

export default Widgets
